.paginator {
    margin: 10px;
    display: flex;
}
.otherPage {
    font-size: 18px;
    border: 1px solid black;
    padding: 3px;
    cursor: pointer;
}

.selectedPage {
    font-size: 18px;
    margin: 1px;
    padding: 3px;
    border: 1px solid black;
    font-weight: bold;
    cursor:pointer;
    background-color: rgba(0, 0, 255, 0.418);
}